.container {
    margin-top: 20px;
    font-family: RobotoMonoSemiBold;
    font-weight: bold;
    font-size: 2.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
    height: 100vh;
}

.header {
    font-weight: bold;
    overflow:visible;
}

.type {
    height: 9rem;
    margin-top: 10px;
}

.myButton {
    font-size: 2rem;
    color: #b8b8b8;
    background-color:#35332f;
    border: none;
    outline: none;
    border-radius: 4px;
    transition: .5s;
    padding: 5px;
    width: 140px;
}
.myButton:hover {
    background-color: #494742;
    box-shadow: 4px 6px 14px 2px rgba(0,0,0,0.7);
    transition: .5s;
}