@font-face {
  font-family: "RobotoMonoSemiBold";
  src: url("./assets/fonts/RobotoMono-SemiBold.ttf");
}

@font-face {
  font-family: "RobotoMonoMedium";
  src: url("./assets/fonts/RobotoMono-Medium.ttf");
}

@font-face {
  font-family: "RobotoMonoLight";
  src: url("./assets/fonts/RobotoMono-Light.ttf");
}

@font-face {
  font-family: "RobotoMonoRegular";
  src: url("./assets/fonts/RobotoMono-Regular.ttf");
}

body {
  color: #9B9B9B;
    overflow-x:hidden;  
  background-color:#181818;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
