.container {
    margin: 40px 20px;
    padding: 20px;
    max-width: 23rem;
    background-color: black;
    border : 2px solid black;
    border-radius: 40px;
    text-align: center;
    font-family: RobotoMonoRegular;
}

.container img {
    max-width: 100%;
    margin-bottom: 10px;
}

.container h3 {
    font-weight: bold;
}

.container p {
    font-size: 1.01rem;
}

.links {
    margin: auto;
    max-width: 50%;
    height: 3rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
